import { IgcGridToolbarComponent } from './igc-grid-toolbar-component';
import { GridToolbar } from './GridToolbar';
import { TypeRegistrar } from 'igniteui-core/type';

export class IgcGridToolbarModule {
    public static register(): void {
        IgcGridToolbarComponent.register();
        TypeRegistrar.registerCons("IgcGridToolbarComponent", IgcGridToolbarComponent);
        TypeRegistrar.register("GridToolbar", (<any>GridToolbar).$type);
    }
}
