import { IgcColumnDependenciesModule } from './igc-column-dependencies-module';

import { IgcColumnGroupComponent } from './igc-column-group-component';
import { ColumnGroup } from './ColumnGroup';
import { TypeRegistrar } from 'igniteui-core/type';

export class IgcColumnGroupModule {
    public static register(): void {
IgcColumnDependenciesModule.register();
        IgcColumnGroupComponent.register();
        TypeRegistrar.registerCons("IgcColumnGroupComponent", IgcColumnGroupComponent);
        TypeRegistrar.register("ColumnGroup", (<any>ColumnGroup).$type);
    }
}
