import { IgcColumnDependenciesModule } from './igc-column-dependencies-module';

import { IgcColumnComponent } from './igc-column-component';
import { Column } from './Column';
import { TypeRegistrar } from 'igniteui-core/type';

export class IgcColumnModule {
    public static register(): void {
IgcColumnDependenciesModule.register();
        IgcColumnComponent.register();
        TypeRegistrar.registerCons("IgcColumnComponent", IgcColumnComponent);
        TypeRegistrar.register("Column", (<any>Column).$type);
    }
}
