import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { IgcGridActionsBaseDirective } from "./igc-grid-actions-base-directive";
import { GridEditingActions as GridEditingActions_internal } from "./GridEditingActions";
import { ensureBool } from "igniteui-core/componentUtil";

/**
 * Grid Editing Actions for the Action Strip
 * @igxParent IgxActionStripComponent
*/
export class IgcGridEditingActions extends IgcGridActionsBaseDirective
{

protected createImplementation() : GridEditingActions_internal
{
	return new GridEditingActions_internal();
}
	/**
	                             * @hidden 
	                             */
	                            public get i() : GridEditingActions_internal {
		return this._implementation;
	}
	constructor() {
		super();
	}
	/**
	 * An input to enable/disable action strip row adding button
	*/
	get addRow() : boolean {
		return (this.i.i as boolean);
	}
	set addRow(v: boolean) {
		this.i.i = ensureBool(v);
	}
	/**
	 * An input to enable/disable action strip row editing button
	*/
	get editRow() : boolean {
		return (this.i.k as boolean);
	}
	set editRow(v: boolean) {
		this.i.k = ensureBool(v);
	}
	/**
	 * An input to enable/disable action strip row deleting button
	*/
	get deleteRow() : boolean {
		return (this.i.j as boolean);
	}
	set deleteRow(v: boolean) {
		this.i.j = ensureBool(v);
	}
	/**
	 * An input to enable/disable action strip child row adding button
	*/
	get addChild() : boolean {
		return (this.i.h as boolean);
	}
	set addChild(v: boolean) {
		this.i.h = ensureBool(v);
	}
	/**
	 * Enter row or cell edit mode depending the grid rowEditable option
	 * @example
	 * ```typescript
	 * this.gridEditingActions.startEdit();
	 * ```
	
	*/
	public startEdit(evt: any) {
		this.i.l(evt);
	}
}
