import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { IgcColumnComponent } from "./igc-column-component";
import { IgcBaseEventArgsDetail } from "./igc-base-event-args-detail";
import { ColumnToggledEventArgsDetail as ColumnToggledEventArgsDetail_internal } from "./ColumnToggledEventArgsDetail";
import { ensureBool } from "igniteui-core/componentUtil";

/**
 * Event emitted when a checkbox in the checkbox
 * list of an IgxColumnActions component is clicked.
*/
export class IgcColumnToggledEventArgsDetail extends IgcBaseEventArgsDetail
{

protected createImplementation() : ColumnToggledEventArgsDetail_internal
{
	return new ColumnToggledEventArgsDetail_internal();
}
	/**
	                             * @hidden 
	                             */
	                            public get i() : ColumnToggledEventArgsDetail_internal {
		return this._implementation;
	}
	constructor() {
		super();
	}
	/**
	 * The column that is toggled.
	*/
	get column() : IgcColumnComponent {
	                                        const r = this.i.g;
	                                        if (r == null) {
	                                            return null;
	                                        }
	                                        
	                                        if (!(r as any).externalObject) {
	                                            let e = IgcColumnComponent._createFromInternal(r);
	                                            if (e) {
	                                                (e as any)._implementation = r;
	                                            }
	                                            (r as any).externalObject = e;
	                                        }
		return (r as any).externalObject;
	}
	set column(v: IgcColumnComponent) {
		v == null ? this.i.g = null : this.i.g = v.i;
	}
	/**
	 * The checked state after the action.
	*/
	get checked() : boolean {
		return (this.i.h as boolean);
	}
	set checked(v: boolean) {
		this.i.h = ensureBool(v);
	}
	
	    public findByName(name: string): any {
	        
	    var baseResult = super.findByName(name);
	    if (baseResult)
	    {
	        return baseResult;
	    }
	
	if (this.column && (this.column as any).name && (this.column as any).name == name) { return this.column; }
	
	        return null;
	    }
}
