export class IgcToggleViewEventArgsDetail extends IgcBaseEventArgsDetail
{

protected createImplementation() : ToggleViewEventArgsDetail_internal
{
	return new ToggleViewEventArgsDetail_internal();
}
	/**
	                             * @hidden 
	                             */
	                            public get i() : ToggleViewEventArgsDetail_internal {
		return this._implementation;
	}
	constructor() {
		super();
	}
	/**
	 * Id of the toggle view
	*/
	get id() : string {
		return (this.i.g as string);
	}
	set id(v: string) {
		this.i.g = v;
	}
}
