import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { delegateCombine, delegateRemove } from 'igniteui-core/type';
import { IgcPivotDimension } from "./igc-pivot-dimension";
import { IgcFilteringExpressionsTree } from "./igc-filtering-expressions-tree";
import { SortingDirection, SortingDirection_$type } from "./SortingDirection";
import { GridColumnDataType, GridColumnDataType_$type } from "./GridColumnDataType";
import { PivotDimensionDetail as PivotDimensionDetail_internal } from "./PivotDimensionDetail";
import { ensureBool, ensureEnum } from "igniteui-core/componentUtil";

/**
 * Configuration of a pivot dimension.
*/
export class IgcPivotDimensionDetail
{

protected createImplementation() : PivotDimensionDetail_internal
{
	return new PivotDimensionDetail_internal();
}
	protected _implementation: any;
	                            /**
	                             * @hidden 
	                             */
	public get i() : PivotDimensionDetail_internal {
		return this._implementation;
	}
	private onImplementationCreated() {
		
	}
	constructor() {
	this._implementation = this.createImplementation();
	(<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	                                if ((this as any)._initializeAdapters) {
	                                    (this as any)._initializeAdapters();
	                                }
	
	}
	protected _provideImplementation(i: any) {
	    this._implementation = i;
	    (<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	                                if ((this as any)._initializeAdapters) {
	                                    (this as any)._initializeAdapters();
	                                }
	
	}
	/**
	 * Allows defining a hierarchy when multiple sub groups need to be extracted from single member.
	*/
	get childLevel() : IgcPivotDimension {
	                                        const r = this.i.d;
	                                        if (r == null) {
	                                            return null;
	                                        }
	                                        
	                                        if (!(r as any).externalObject) {
	                                            let e = new IgcPivotDimension();
	                                            if ((r as any).$type) {
	                                                (e as any)._implementation = r;
	                                            } else {
	                                                if ((e.i as any).setNativeElement) {
	                                                    (e.i as any).setNativeElement(r);
	                                                }
	                                            }
	                                            (r as any).externalObject = e;
	                                        }
		return (r as any).externalObject;
	}
	set childLevel(v: IgcPivotDimension) {
		v == null ? this.i.d = null : this.i.d = v.i;
	}
	/**
	 * Field name to use in order to extract value.
	*/
	get memberName() : string {
		return (this.i.l as string);
	}
	set memberName(v: string) {
		this.i.l = v;
	}
	/**
	 * Enables/Disables a particular dimension from pivot structure.
	*/
	get enabled() : boolean {
		return (this.i.h as boolean);
	}
	set enabled(v: boolean) {
		this.i.h = ensureBool(v);
	}
	/**
	 * A predefined or defined via the `igxPivotSelector` filter expression tree for the current dimension to be applied in the filter pipe.
	*/
	get filter() : IgcFilteringExpressionsTree {
	                                        const r = this.i.a;
	                                        if (r == null) {
	                                            return null;
	                                        }
	                                        
	                                        if (!(r as any).externalObject) {
	                                            let e = new IgcFilteringExpressionsTree();
	                                            if ((r as any).$type) {
	                                                (e as any)._implementation = r;
	                                            } else {
	                                                if ((e.i as any).setNativeElement) {
	                                                    (e.i as any).setNativeElement(r);
	                                                }
	                                            }
	                                            (r as any).externalObject = e;
	                                        }
		return (r as any).externalObject;
	}
	set filter(v: IgcFilteringExpressionsTree) {
		v == null ? this.i.a = null : this.i.a = v.i;
	}
	/**
	 * The sorting direction of the current dimension. Determines the order in which the values will appear in the related dimension.
	*/
	get sortDirection() : SortingDirection {
		return this.i.e;
	}
	set sortDirection(v: SortingDirection) {
		this.i.e = ensureEnum<SortingDirection>(SortingDirection_$type, v);
	}
	/**
	 * The dataType of the related data field.
	*/
	get dataType() : GridColumnDataType {
		return this.i.b;
	}
	set dataType(v: GridColumnDataType) {
		this.i.b = ensureEnum<GridColumnDataType>(GridColumnDataType_$type, v);
	}
	/**
	 * The width of the dimension cells to be rendered.Can be pixel or %.
	*/
	get width() : string {
		return (this.i.m as string);
	}
	set width(v: string) {
		this.i.m = v;
	}
	/**
	 * Level of the dimension.
	*/
	get level() : number {
		return (this.i.i as number);
	}
	set level(v: number) {
		this.i.i = +v;
	}
	
	    public findByName(name: string): any {
	        
	    if ((this as any).findEphemera) {
	        if (name && name.indexOf("@@e:") == 0) {
	            return (this as any).findEphemera(name);
	        }
	    }
	
	if (this.childLevel && (this.childLevel as any).name && (this.childLevel as any).name == name) { return this.childLevel; }
	if (this.filter && (this.filter as any).name && (this.filter as any).name == name) { return this.filter; }
	
	        return null;
	    }
	public setNativeElement(element: any) {
		this.i.setNativeElement(element);
	}
}
