import { IgcColumnDependenciesModule } from './igc-column-dependencies-module';

import { IgcColumnLayoutComponent } from './igc-column-layout-component';
import { ColumnLayout } from './ColumnLayout';
import { TypeRegistrar } from 'igniteui-core/type';

export class IgcColumnLayoutModule {
    public static register(): void {
IgcColumnDependenciesModule.register();
        IgcColumnLayoutComponent.register();
        TypeRegistrar.registerCons("IgcColumnLayoutComponent", IgcColumnLayoutComponent);
        TypeRegistrar.register("ColumnLayout", (<any>ColumnLayout).$type);
    }
}
