import { IgcSummaryTemplateContext } from './igc-summary-template-context';
import { IgcCellTemplateContext } from './igc-cell-template-context';
import { IgcColumnTemplateContext } from './igc-column-template-context';
import { IgcTemplateContainerModule } from 'igniteui-core/igc-template-container-module';
import { TypeRegistrar } from 'igniteui-core/type';


export class IgcColumnDependenciesModule {
    public static register() {
                TypeRegistrar.registerCons('IgcSummaryTemplateContext', IgcSummaryTemplateContext);
        TypeRegistrar.registerCons('IgcCellTemplateContext', IgcCellTemplateContext);
        TypeRegistrar.registerCons('IgcColumnTemplateContext', IgcColumnTemplateContext);
        IgcTemplateContainerModule.register();

    }
}