/**
 * Provides a pre-configured exporter component for the grid.
 * @remarks
 * This component still needs the actual exporter service(s) provided in the DI chain
 * in order to export something.
 * @igxModule IgxGridToolbarModule
 * @igxParent IgxGridToolbarComponent
*/
export class IgcGridToolbarExporter extends IgcBaseToolbarDirective
{

protected createImplementation() : GridToolbarExporter_internal
{
	return new GridToolbarExporter_internal();
}
	/**
	                             * @hidden 
	                             */
	                            public get i() : GridToolbarExporter_internal {
		return this._implementation;
	}
	constructor() {
		super();
	}
	/**
	 * Show entry for CSV export.
	*/
	get exportCSV() : boolean {
		return (this.i.w as boolean);
	}
	set exportCSV(v: boolean) {
		this.i.w = ensureBool(v);
	}
	/**
	 * Show entry for Excel export.
	*/
	get exportExcel() : boolean {
		return (this.i.x as boolean);
	}
	set exportExcel(v: boolean) {
		this.i.x = ensureBool(v);
	}
	/**
	 * The name for the exported file.
	*/
	get filename() : string {
		return (this.i.z as string);
	}
	set filename(v: string) {
		this.i.z = v;
	}
	/**
	 * Indicates whether there is an export in progress.
	*/
	get isExporting() : boolean {
		return (this.i.y as boolean);
	}
	set isExporting(v: boolean) {
		this.i.y = ensureBool(v);
	}
	public Export(type: GridToolbarExporterType, toggleRef: IgcToggleDirective) {
		this.i.ab((type == null ? null : type), (toggleRef == null ? null : toggleRef.i));
	}
	
	    private _exportStarted: (s: IgcGridToolbarExporter, e: IgcExporterEventArgs) => void = null;
	    private _exportStarted_wrapped: any = null;
	/**
	 * Emitted when starting an export operation. Re-emitted additionally
	 * by the grid itself.
	*/
	
	public get exportStarted(): (s: IgcGridToolbarExporter, e: IgcExporterEventArgs) => void {
	return this._exportStarted;
	}
	    public set exportStarted(ev: (s: IgcGridToolbarExporter, e: IgcExporterEventArgs) => void) {
	        if (this._exportStarted_wrapped !== null) {
	            this.i.exportStarted = delegateRemove(this.i.exportStarted, this._exportStarted_wrapped);
	            this._exportStarted_wrapped = null;
	            this._exportStarted = null;
	        }
	
	        this._exportStarted = ev;
	        this._exportStarted_wrapped = (o, e) => {
	            
	                    let outerArgs = new IgcExporterEventArgs();
	(<any>outerArgs)._provideImplementation(e);
	                    if ((this as any).beforeExportStarted) {
	                        (this as any).beforeExportStarted(this, outerArgs);
	                    }
	                    if (this._exportStarted) {
	    this._exportStarted(this, outerArgs);
	                    }
	
	        };
	        this.i.exportStarted = delegateCombine(this.i.exportStarted, this._exportStarted_wrapped);
	        if (this.i.exportStartedChanged) {
	            this.i.exportStartedChanged();
	        }
	;
	    }
	
	    private _exportEnded: (s: IgcGridToolbarExporter, e: IgcVoidEventArgs) => void = null;
	    private _exportEnded_wrapped: any = null;
	/**
	 * Emitted on successful ending of an export operation.
	*/
	
	public get exportEnded(): (s: IgcGridToolbarExporter, e: IgcVoidEventArgs) => void {
	return this._exportEnded;
	}
	    public set exportEnded(ev: (s: IgcGridToolbarExporter, e: IgcVoidEventArgs) => void) {
	        if (this._exportEnded_wrapped !== null) {
	            this.i.exportEnded = delegateRemove(this.i.exportEnded, this._exportEnded_wrapped);
	            this._exportEnded_wrapped = null;
	            this._exportEnded = null;
	        }
	
	        this._exportEnded = ev;
	        this._exportEnded_wrapped = (o, e) => {
	            
	                    let outerArgs = new IgcVoidEventArgs();
	(<any>outerArgs)._provideImplementation(e);
	                    if ((this as any).beforeExportEnded) {
	                        (this as any).beforeExportEnded(this, outerArgs);
	                    }
	                    if (this._exportEnded) {
	    this._exportEnded(this, outerArgs);
	                    }
	
	        };
	        this.i.exportEnded = delegateCombine(this.i.exportEnded, this._exportEnded_wrapped);
	        if (this.i.exportEndedChanged) {
	            this.i.exportEndedChanged();
	        }
	;
	    }
}
