import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { IgcGroupByResult } from "./igc-group-by-result";
import { IgcGroupingState } from "./igc-grouping-state";
import { IgcGridSortingStrategy } from "./igc-grid-sorting-strategy";
import { GridGroupingStrategy as GridGroupingStrategy_internal } from "./GridGroupingStrategy";

/**
 * Represents a grouping strategy for the grid data, extending the Sorting Strategy interface (contains a sorting method).
*/
export class IgcGridGroupingStrategy extends IgcGridSortingStrategy
{

protected createImplementation() : GridGroupingStrategy_internal
{
	
let impl = new GridGroupingStrategy_internal();
if ((impl as any).setNativeElement) {
    (impl as any).setNativeElement({});
}
return impl;

}
	/**
	                             * @hidden 
	                             */
	                            public get i() : GridGroupingStrategy_internal {
		return this._implementation;
	}
	constructor() {
		super();
	}
	public groupBy(data: any[], state: IgcGroupingState, grid: any, groupsRecords: any[], fullResult: IgcGroupByResult) : IgcGroupByResult {
	                        
		let iv = this.i.g(data, (state == null ? null : state.i), grid, groupsRecords, (fullResult == null ? null : fullResult.i));
	                        let ret: IgcGroupByResult = null;
	                                if (iv && (iv as any).externalObject) {
	                                    ret = (iv as any).externalObject;
	                                } else {
	                                    if (iv) {
	                                        let e = new IgcGroupByResult();
	                                        (e as any)._implementation = iv;
	                                        (iv as any).externalObject = e;
	                                        ret = e as IgcGroupByResult;
	                                    }
	                                }
	                                return ret;
	}
}
