import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { IgcGridToolbarContent } from "./igc-grid-toolbar-content";
import { GridToolbarTitle as GridToolbarTitle_internal } from "./GridToolbarTitle";

/**
 * Provides a way to template the title portion of the toolbar in the grid.
 * @igxModule IgxGridToolbarModule
 * @igxParent IgxGridToolbarComponent
 * @example
 * ```html
 * My custom title
 * ```
*/
export class IgcGridToolbarTitle extends IgcGridToolbarContent
{

protected createImplementation() : GridToolbarTitle_internal
{
	return new GridToolbarTitle_internal();
}
	/**
	                             * @hidden 
	                             */
	                            public get i() : GridToolbarTitle_internal {
		return this._implementation;
	}
	constructor() {
		super();
	}
	get name() : string {
		return (this.i.a as string);
	}
	set name(v: string) {
		this.i.a = v;
	}
	public setNativeElement(element: any) {
		this.i.setNativeElement(element);
	}
}
