import { BaseToolbarDirective } from "./BaseToolbarDirective";
import { Base, Type, markType } from "igniteui-core/type";
import { WCNativeHelper } from "igniteui-webinputs/WCNativeHelper";

/**
 * @hidden 
 */
export class BaseToolbarColumnActionsDirective extends BaseToolbarDirective {
	static $t: Type = markType(BaseToolbarColumnActionsDirective, 'BaseToolbarColumnActionsDirective', (<any>BaseToolbarDirective).$);
	protected get_a(): string {
		let ret_ = this.i.n("name");
		return <string>ret_;
	}
	protected set_a(a: string): void {
		let value_ = <any>a;
		this.i.v("name", value_);
	}
}


