import { IgcPaginatorComponent } from './igc-paginator-component';
import { Paginator } from './Paginator';
import { TypeRegistrar } from 'igniteui-core/type';

export class IgcPaginatorModule {
    public static register(): void {
        IgcPaginatorComponent.register();
        TypeRegistrar.registerCons("IgcPaginatorComponent", IgcPaginatorComponent);
        TypeRegistrar.register("Paginator", (<any>Paginator).$type);
    }
}
