import { IgcActionStripComponent } from './igc-action-strip-component';
import { ActionStrip } from './ActionStrip';
import { TypeRegistrar } from 'igniteui-core/type';

export class IgcActionStripModule {
    public static register(): void {
        IgcActionStripComponent.register();
        TypeRegistrar.registerCons("IgcActionStripComponent", IgcActionStripComponent);
        TypeRegistrar.register("ActionStrip", (<any>ActionStrip).$type);
    }
}
